import SearchHeader from '@/components/seach-header/seach-header'

import axios from '@/api/axiosApi'
import { localTag } from '@/assets/js/local'

export default {
  data () {
    return {
      hotTags: [],
      myTags: [],
      matchTags: [],
      macthTop: 0,
      showMatch: false
    }
  },
  mounted () {
    this.macthTop = this.$refs.header.$el.clientHeight
  },
  components: {
    SearchHeader
  },
  created () {
    this._getHotTag()
    this.myTags = localTag.get() || []
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    _getHotTag () {
      axios.get(this, '/v1/tag', {
        hotTag: 1,
        pageSize: 10
      }, (data) => {
        this.hotTags = data
      })
    },
    search (value) {
      this.showMatch = true
      axios.get(this, '/v1/tag', {
        pageSize: 10,
        name: value
      }, (data) => {
        this.matchTags = data
      })
    },
    removeSearch () {
      this.showMatch = false
    },
    selectTag (tag) {
      const findIndex = this.$parent.tags.findIndex(item => {
        return item.id === tag.id
      })
      if (findIndex < 0) {
        this.$parent.tags.push({
          id: tag.id,
          name: tag.name
        })
      }
      this.$router.go(-1)
    },
    toDeleteTag (tag) {
      if (this.longTouch) {
        this.clearLongTouch()
      }
      const _self = this
      this.longTouch = setTimeout(() => {
        localTag.remove(tag.id)
        _self.myTags = localTag.get() || []
      }, 1500)
    },
    clearLongTouch () {
      clearInterval(this.longTouch)
    },
    removeAllTag () {
      localTag.removeAll()
      this.myTags = localTag.get() || []
    }
  }
}



